import React from 'react'
import '../css/work.css'
import '../css/style.css'
import $ from 'jquery'


$(document).ready(function(){
    console.log("workReady!");



})
function Work() {
    return (
        <div className="work">
            <div className="earth-background">
                <div className="background__poasition">
                    <div className="background__image"></div>
                </div>
            </div>
            <div className="Earth-Text">
            <div className="whole100vh">
                <nav>
                    <div className="ourEarthPosition">
                        <div className="h1Position">
                            <h1>The Earth</h1>
                        </div>
                    </div>
                </nav>
                <main>
                    <div className="main__text__button">
                        <div className="h2__main__text">
                            <h2 className="top__top__top__earth">The earth is the third planet from our sun. </h2>
                            <h2 className="smallerFont">About 29.2% of Earth's surface is land consisting of continents and islands.</h2>
                            <h2 className="smallerFont">The remaining 70.8% is covered with water</h2>
                        </div>
                        <div className="main__text__button">
                            <button>learn more</button>
                        </div>
                    </div>
                </main>
            </div>
        </div>
        <div className="Mars-Background">
            <div className="background__position">
                <div className="mars__background"></div>
            </div>
        </div>
        <div className="mars-text">
            <div className="mars__position">
                <h2 className="header__mars">The Mars</h2>
                <h2 className="top__top__top__mars">Mars is the fourth planet from the Sun and the second-smallest planet in the Solar System.</h2>
                <div className="button__position">
                    <button>learn more</button>
                </div>
            </div>
        </div>
        </div>
        
    )
}

export default Work
